.footer {
    background-color: #333;
    color: white;
    padding: 20px;
    text-align: center;
    margin-top: 406px;
  }
  
  .footer-content {
    display: flex;
    justify-content: space-around;
  }
  
  .footer-section {
    max-width: 300px;
  }

  .footer-section a{
    color: white; 
    text-decoration: none;
  }
  
  @media only screen and (max-width: 600px) {
  
    footer {
      font-size: 14px;
    }
  }