body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

header {
  background-color: #000033;
  color: white;
  padding: 10px;
  text-align: right; 
}

nav {
  display: flex;
  justify-content: flex-end;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex; 
}

nav li {
  margin-right: 50px; 
  display: flex; 
}

nav a {
  color: white; 
  text-decoration: none;
  font-size: 18px; 
  font-weight: bold; 
  font-family: 'Roboto', sans-serif; 
}

main {
  flex: 1;
  padding: 20px;
}


/* Media Queries for Responsive Design */
@media only screen and (max-width: 600px) {
  nav {
    flex-direction: column;
  }

  nav li {
    margin: 5px 0;
  }

  header {
    font-size: 16px;
  }

  footer {
    font-size: 14px;
  }
}
